class DeclensionHelper {
  static daysWord = number => {
    const lastDigit = DeclensionHelper.lastDigit(number)
    
    switch(lastDigit) {
      case '0':
        return ''
      case '1':
        return ''
      case '2':
      case '3':
      case '4':
        return ''
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        return ''
      default:
        return ' '
    }
  }
  
  static workWord = number => {
    const lastDigit = DeclensionHelper.lastDigit(number)
    
    switch(lastDigit) {
      case '1':
        return ''
      case '0':
      case '2':
      case '3':
      case '4':
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        return ''
      default:
        return ' '
    }
  }
  
  static workWeekWord = number => {
    const lastDigit = DeclensionHelper.lastDigit(number)
    
    switch(lastDigit) {
      case '1':
        return ''
      case '0':
      case '2':
      case '3':
      case '4':
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        return ''
      default:
        return ' '
    }
  }
  
  static leftWord = number => {
    const lastDigit = DeclensionHelper.lastDigit(number)
    
    switch(lastDigit) {
      case '1':
        return ''
      case '0':
      case '2':
      case '3':
      case '4':
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        return ''
      default:
        return ' '
    }
  }
  
  static leftWeekWord = number => {
    const lastDigit = DeclensionHelper.lastDigit(number)
    
    switch(lastDigit) {
      case '1':
        return ''
      case '0':
      case '2':
      case '3':
      case '4':
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        return ''
      default:
        return ' '
    }
  }
  
  static weekWord = number => {
    const lastDigit = DeclensionHelper.lastDigit(number)
  
    switch(lastDigit) {
      case '0':
        return ''
      case '1':
        return ''
      case '2':
      case '3':
      case '4':
        return ''
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        return ''
      default:
        return ' '
    }
  }
  
  static lastDigit = number => {
    return number.toString().split('').pop()
  }
}

export default DeclensionHelper



